@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: black;
  background-image: url('https://miro.medium.com/max/3840/1*PyExFMlUFx1Yv_28Nd1BRA.png');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.App {
  min-height: 100vh;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.vertical-timeline-element-content{display:flex;flex-direction:column;width:100%}.vertical-timeline-element-title{text-align:left;margin-bottom:1rem;width:100%}.vertical-timeline-element--work{max-height:400px}.thumbnail iframe{width:1440px;height:900px}.thumbnail{max-height:250px;position:relative;-ms-zoom:0.25;-moz-transform:scale(0.25);-moz-transform-origin:0 0;-o-transform:scale(0.25);-o-transform-origin:0 0;-webkit-transform:scale(0.25);-webkit-transform-origin:0 0}

.navbar{font-family:'Russo One', sans-serif;display:flex;flex-direction:row;justify-content:space-between;background-color:black;height:80px;top:0;width:100%;position:sticky;z-index:1;box-shadow:1px 9px 21px 0px rgba(255,243,243,0.75);opacity:0.9;transition:1500ms}.navbar__logo{align-self:center;max-height:80px;color:white;cursor:pointer;margin-left:1rem;transition:1000ms}.navbar__logo:hover{border-radius:50px;background-color:rgba(104,104,104,0.556);transition:1000ms}.navbar--scrolled{background:#000;opacity:0.7;transition:1000ms}.navbar-right{margin:0.65rem;margin-right:1rem}.navbar-right>a:hover{fill:blue}.navbar-middle{margin:1rem;font-size:2rem}.navbar-middle>a{text-decoration:none;color:white;margin:1rem;transition:500ms}.navbar-middle>a:hover{color:rgba(0,0,255,0.7);transition:500ms}

